<template>
	<view class="content" :style="{'height':screenHeight}">
		<!-- #ifdef APP-PLUS -->
			<view class="statusBox"></view>
		<!-- #endif -->
		<view class="tops">
			<view class="left">
				<image class="headImage" mode="" :src="userData.headImage" @tap="goSet"></image>
			</view>
			<view class="center">
				<view class="name top overflow_one">{{userData.name}}</view>
				<view class="name bottom overflow_one">用户id:{{userData.id}}</view>
			</view>
			<view class="back" @tap="backHome">返回首页</view>
			<image class="bottomImage" src="../../static/image/user/bottom.png" mode="" @tap="topImage"></image>
		</view>
		<view class="orderBox">
			<view class="title">
				<view class="titleName">我的订单</view>
				<view class="right" @tap="orderBtn(-1)">查看全部<uni-icons class="icons" type="arrowright" color="rgb(51,51,51)" size="12"/></view>
			</view>
			<view class="clearBoth"></view>
			<view class="orderBtns">
				<view class="boxs" v-for="(item,index) in orderData" :key="index" @tap="orderBtn(index)" :style=" index == 1 ? {'width':'60%'} : {'width':'20%'} ">
					<image class="images" :src="item.src" mode=""></image>
					<view class="name">{{item.name}}</view>
				</view>
				<view class="clearBoth"></view>
			</view>
		</view>
		<view class="toolsBox">
			<view class="rows" v-for="(item,index) in toolsData" :key="index" @tap="toolsBtn(index)">
				<!-- <image class="iconLeft" :src="item.src" mode="" v-if="index !== 0 && index !== 3 && index !== 5"></image> -->
				<image class="iconLefts" :src="item.src" mode=""></image>
				<view class="name">{{item.name}}</view>
				<uni-icons class="icons" type="arrowright" color="rgb(197,197,197)" size="12"/>
				<view class="clearBoth"></view>
			</view>
		</view>
		
		
	</view>
</template>
<style>
	.statusBox{width:100%;height:90upx;background: white;position: fixed;top:0;left: 0;z-index: 2;}
	.content{padding-bottom: 0upx;overflow-x: hidden;background:rgb(244,244,244);height:100%;}
	.content .tops{width:100%;position: relative;display: flex;padding-top:50upx;padding-bottom: 180upx;background:url(../../static/image/user/top.png);background-repeat: no-repeat;background-size: 100% 100%;}
	.content .tops .left{width:auto;float: left;margin-left: 5%;}
	.content .tops .left .headImage{width:108upx;height:108upx;border-radius: 108upx;float: left;}
	.content .center{width:55%;margin:5upx 0 0 20upx;}
	.content .center .name{color:white;}
	.content .center .top{font-size:18px;}
	.content .center .bottom{font-size:12px;margin-top:15upx;}
	.content .back{background: white;color:rgb(78,178,244);padding:0 20upx;border-radius: 100upx;height:50upx;line-height: 50upx;font-size: 12px;margin-left: 3%;margin-top: 20upx;}
	.content .bottomImage{position: absolute;width:90%;height:160upx;bottom:-10%;left:5%}
	.content .orderBox{width:90%;padding:2.5%;margin-left: 2.5%;margin-top:80upx;background: white;border-radius: 5upx;}
	.content .orderBox .titleName{float: left;color: #000000;font-weight: bold;}
	.content .orderBox .right{float: right;font-size: 12px;}
	.content .orderBox .right .icons{position: relative;top:4upx}
	.content .orderBtns{width:100%;display: flex;background: white;border-top:1px dashed rgb(136,136,136);margin-top: 20upx;}
	.content .orderBtns .boxs{justify-content: center;margin-top:30upx;text-align: center;}
	.content .orderBtns .boxs .images{width:55upx;height:50upx;}
	.content .orderBtns .boxs .name{font-size: 12px;}
	.content .toolsBox{width:95%;background:white;border-radius: 10upx;margin-top: 40upx;margin-left: 2.5%;}
	.content .toolsBox .rows{width:95%;border-bottom: 1px solid rgb(242,242,242);padding:4%;padding-left: 5%;}
	.content .toolsBox .rows .iconLeft{width:30upx;height:34upx;float: left;position: relative;top:4upx;margin-right:20upx}
	.content .toolsBox .rows .iconLefts{width:28upx;height:34upx;float: left;position: relative;top:4upx;margin-right:20upx}
	.content .toolsBox .rows .name{font-size: 14px;margin:0 10upx;float: left;}
	.content .toolsBox .rows .icons{float: right;margin-right: 2%;}
	/* #ifdef APP-PLUS */
		.content{padding-bottom: 0upx;overflow-x: hidden;background:rgb(244,244,244);height:100%;margin-top: 90upx;}
	/* #endif */
</style>
<script>
	import uniIcons from '@/components/uni-icons/uni-icons.vue'
	import uniSection from '@/components/uni-section/uni-section.vue'
	export default {
		components:{
			uniIcons,
			uniSection
		},
		data() {
			return {
				screenHeight:"",
				userData:{
					name:"",
					id:"",
					headImage:""
				},
				orderData:[
					{"src":"../../static/image/user/order1.png","name":"待发货"},
					{"src":"../../static/image/user/order2.png","name":"待收货"},
					{"src":"../../static/image/user/order3.png","name":"评价"},
				],
				toolsData:[
					{"src":"../../static/image/user/tools_1.png","name":"代理中心"},
					{"src":"../../static/image/user/tools_2.png","name":"我的收益"},
					{"src":"../../static/image/user/tools_3.png","name":"我的团队"},
					
					{"src":"../../static/image/user/tools_8.png","name":"分享推广"},
					{"src":"../../static/image/user/tools_9.png","name":"全网分红"},
					
					{"src":"../../static/image/user/tools_4.png","name":"我的地址"},
					{"src":"../../static/image/user/tools_5.png","name":"修改登陆密码"},
					{"src":"../../static/image/user/tools_6.png","name":"更换绑定手机"},
					{"src":"../../static/image/user/tools_7.png","name":"咨询客服"},
					// {"src":"../../static/image/user/tools_8.png","name":"分享推广"},
					// {"src":"../../static/image/user/tools_9.png","name":"全网分红"},
				],
				AjaxData:{},
				_$token:"",
			}
		},
		onShow(){
			var that = this;
			const res = uni.getSystemInfoSync();
			this.screenHeight = res.windowHeight*2 - 100 + 'upx'
			this.getStorage();
			setTimeout(function(){
				that.AjaxMsg();
			},100)
		},
		methods: {
			// getStorage(){var that = this;uni.getStorage({ key: 'token',success: function (res) {that._$token = res.data}})},
			getStorage(){
				var that = this;
				try {const value = uni.getStorageSync('token');
					if (value) {
						that._$token = value;
					}else{
						that._$token = ""
					}
				} catch (e) {}},
			AjaxMsg(){
				var that = this;
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
				};
				this._$InitRequest.request('user/get_userInfo', param).then(res =>{
					that.userData.name = res.data.data.nickname
					that.userData.id = res.data.data.id
					that.userData.headImage = res.data.data.headimgurl
					that.AjaxData = res.data.data
					uni.setStorage({
					    key: 'fromUser',
					    data: res.data.data,
					    success: function(){}
					});
				},error=>{})
			},
			backHome(){uni.navigateTo({url:"../index/index"})},
			topImage(){},
			orderBtn(index){
				if( index == -1 ){
					uni.navigateTo({url:"./order/order?index=0"})
				}else if( index == 0 ){
					uni.navigateTo({url:"./order/order?index=1"})
				}else if( index == 1 ){
					uni.navigateTo({url:"./order/order?index=2"})
				}else if( index == 2 ){
					uni.navigateTo({url:"./order/order?index=3"})
				}
			},
			toolsBtn(index){
				if( index == 0 ){
					if( this.AjaxData.agent_type == 0 ){
						uni.navigateTo({url:"./agent/agent"})
					}else{
						uni.navigateTo({url:"./agent/agentCenter"})
					}
				}else if( index == 1 ){
					uni.navigateTo({url:"./myProceeds/myProceeds"})
				}else if( index == 2 ){
					uni.navigateTo({url:"./myItem/myItem"})
				}else if( index == 3 ){
					uni.navigateTo({url:"./share/share"})
				}else if( index == 4 ){
					uni.navigateTo({url:"./dividends/dividends"})
				}else if( index == 5 ){
					uni.navigateTo({url:"./myAddr/myAddr"})
				}else if( index == 6 ){
					uni.navigateTo({url:"./forgetPassWord/forgetPassWord"})
				}else if( index == 7 ){
					uni.navigateTo({url:"./changTel/changTel"})
				}else if( index == 8 ){
					uni.navigateTo({
						url:'./socket/socket'
					})
				}
			},
			goSet(){uni.navigateTo({url:"./set/set"})},
			
			
			
			
		}
	}
</script>